/* Banner.css */

.banner {
    position: relative;
    width: 100%;
    height:40vh;
    /* Adjust the height as needed */
    overflow: hidden;
    margin-left: 1rem;
}




.content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: white;
}
.content button{
    padding: 8px;
    background-color: rgb(252, 183, 8);
    color:'black';
    outline: none;
    border: none;
    box-shadow: 5px 2px rgb(3, 3, 3);
}
.content button:hover{
    background-color: black;
    color: white;
    box-shadow: 5px 2px darkorange;
    transition: all 0.5s ease;
}
.video-container {
    position: relative;
    width: 100%;
    height: 100vh;
    /* Adjust the height as needed */
    overflow: hidden;
}

.dark-blur-video {
    /* filter: brightness(0.8) blur(3px); */
    /* Adjust the values for darkness and blur */
    width: 100%;
    height: auto;
}

.carousel-inner{
    position: relative;
}
.C-Item{
    position: absolute;
   top: 35%;
   left: 15%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  width: 70%;
}
/* ImageZoomComponent.css */

.image-zoom-container {
    overflow: hidden;
    position: relative;
    cursor: pointer;
}

.zoomable-image {
    transition: transform 0.3s ease-in-out;
    /* Adjust the width as needed */
}

.zoomable-image:hover {
    transform: deg(50);
    /* Adjust the scale factor for the zoom effect */
}

.card{
    position: absolute;
    top: 12%;
    left: 25%;
    background-color: transparent;
    color: white;
    text-align: center;
}